import supabase from "../config/supabaseConfig.js"; // Corrigido: Importao default do caminho correto

/**
 * Funo para criar um novo usurio professor com o email prof@ironhouse.com
 */
export async function criarProfessorCorreto() {
  try {
    // Definir email e senha que correspondem aos usados na tentativa de login
    const email = "prof@ironhouse.com";
    const senha = "Professor@2025"; // Voc deve alterar esta senha aps o login

    // Criar usurio professor no Supabase
    // eslint-disable-next-line no-unused-vars
    const { data, error } = await supabase.auth.signUp({
      email: email,
      password: senha,
      options: {
        data: {
          nome: "Professor IronHouse",
          role: "professor",
        },
      },
    });

    if (error) {
      return { success: false, error: error.message };
    }

    return {
      success: true,
      message: "Professor criado com sucesso",
      data: { email, senha },
    };
  } catch (error) {
    return { success: false, error: error.message };
  }
}

/**
 * Funo para verificar se o usurio professor existe
 */
export async function verificarProfessor() {
  try {
    // Usar admin para verificar usurios no  recomendado em produo
    // Este  apenas um exemplo de verificao simplificada
    const { data, error } = await supabase.auth.signInWithPassword({
      email: "prof@ironhouse.com",
      password: "Professor@2025",
    });

    if (error) {
      return { success: false, error: error.message };
    }

    return { success: true, data };
  } catch (error) {
    return { success: false, error: error.message };
  }
}
