// Configurao do Supabase para diferentes ambientes
// Este arquivo centraliza a configurao de URLs e chaves para facilitar o deploy

import { createClient } from "@supabase/supabase-js";

// A chave annima do Supabase  segura para incluir no cliente
// Nunca inclua chaves de servio ou chaves com privilgios elevados no front-end

// Chave annima correta para desenvolvimento e produo
const SUPABASE_URL = "https://rnvsemzycvhuyeatjkaq.supabase.co";
const SUPABASE_ANON_KEY =
  "eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJpc3MiOiJzdXBhYmFzZSIsInJlZiI6InJudnNlbXp5Y3ZodXllYXRqa2FxIiwicm9sZSI6ImFub24iLCJpYXQiOjE3NDQ4MDk0NTAsImV4cCI6MjA2MDM4NTQ1MH0.d2GTmTBAUIINoL52Ylz4tXsnhPLBTynOtvKlVa5sy60";

// Determinar qual ambiente est sendo usado
const environment =
  process.env.NODE_ENV === "production" ? "production" : "development";

// Obter a URL e a chave para o ambiente atual
const supabaseUrl =
  environment === "production"
    ? SUPABASE_URL
    : process.env.REACT_APP_SUPABASE_URL || SUPABASE_URL;

const supabaseAnonKey =
  environment === "production"
    ? SUPABASE_ANON_KEY
    : process.env.REACT_APP_SUPABASE_KEY || SUPABASE_ANON_KEY;

// Criar e exportar a instncia do cliente Supabase
const supabase = createClient(supabaseUrl, supabaseAnonKey);

export default supabase;

// Exportar URL e Chave separadamente (opcional, se ainda forem necessrios em algum lugar)
export { supabaseUrl, supabaseAnonKey };
