import supabase from "../config/supabaseConfig.js"; // Corrigido: Importao default do caminho correto

/**
 * Servio para gerenciar a relao entre aulas e alunos utilizando a tabela de juno aula_alunos
 */
export const aulaAlunosService = {
  /**
   * Adiciona um aluno a uma aula
   * @param {string} aulaId - ID da aula
   * @param {string} alunoId - ID do aluno
   * @returns {Promise<Object>} - Os dados da relao criada
   */
  async adicionarAluno(aulaId, alunoId) {
    try {
      // Verificar se os IDs so vlidos
      if (!aulaId || !alunoId) {
        return {
          error: true,
          message: "IDs de aula ou aluno invlidos",
          details: { aulaId, alunoId },
        };
      }

      const { data, error } = await supabase
        .from("aula_alunos")
        .insert([{ aula_id: aulaId, aluno_id: alunoId }])
        .select()
        .single();

      if (error) {
        // Se o erro for de duplicao (aluno j na aula), retorna um objeto de erro especfico
        if (error.code === "23505") {
          // Cdigo para violao de restrio unique
          return {
            error: true,
            message: "Este aluno j est na aula",
            isDuplicate: true,
          };
        }
        return {
          error: true,
          message: error.message || "Erro ao adicionar aluno  aula",
          details: error,
        };
      }

      return data;
    } catch (err) {
      return {
        error: true,
        message: err.message || "Erro inesperado ao adicionar aluno",
        details: err,
      };
    }
  },

  /**
   * Remove um aluno de uma aula
   * @param {string} aulaId - ID da aula
   * @param {string} alunoId - ID do aluno
   * @returns {Promise<boolean>} - Verdadeiro se a remoo foi bem-sucedida
   */
  async removerAluno(aulaId, alunoId) {
    try {
      // Validar parmetros
      if (!aulaId || !alunoId) {
        return false;
      }

      // Verificar se a relao existe antes de tentar remover
      const { data: existingRelation, error: checkError } = await supabase
        .from("aula_alunos")
        .select("*")
        .eq("aula_id", aulaId)
        .eq("aluno_id", alunoId)
        .maybeSingle();

      if (checkError) {
        throw checkError;
      }

      // Se no existe relao, no precisamos fazer nada
      if (!existingRelation) {
        return true;
      }

      // Remover a relao
      const { error } = await supabase
        .from("aula_alunos")
        .delete()
        .match({ aula_id: aulaId, aluno_id: alunoId });

      if (error) {
        throw error;
      }

      return true;
    } catch (err) {
      throw err;
    }
  },

  /**
   * Obtm todos os alunos de uma aula
   * @param {string} aulaId - ID da aula
   * @returns {Promise<Array>} - Lista de alunos com dados completos
   */
  async getAlunosDaAula(aulaId) {
    try {
      // Validar parmetro
      if (!aulaId) {
        return [];
      }

      const { data, error } = await supabase
        .from("aula_alunos")
        .select("aluno_id")
        .eq("aula_id", aulaId);

      if (error) {
        throw error;
      }

      if (!data || data.length === 0) {
        return [];
      }

      // Obter os dados completos dos alunos
      const alunoIds = data.map((item) => item.aluno_id);

      const { data: alunos, error: alunosError } = await supabase
        .from("alunos")
        .select("*")
        .in("id", alunoIds);

      if (alunosError) {
        throw alunosError;
      }

      return alunos || [];
    } catch (err) {
      // Retornar array vazio em vez de propagar o erro para evitar quebrar a UI
      return [];
    }
  },

  /**
   * Atualiza a lista de alunos de uma aula
   * @param {string} aulaId - ID da aula
   * @param {Array<string>} alunoIds - Lista de IDs de alunos
   * @returns {Promise<boolean>} - Verdadeiro se a atualizao foi bem-sucedida
   */
  async atualizarAlunosDaAula(aulaId, alunoIds) {
    try {
      // Validar entrada
      if (!aulaId) {
        return false;
      }

      if (!Array.isArray(alunoIds)) {
        return false;
      }

      // Filtrar apenas IDs vlidos (no nulos, no undefined, no vazios)
      const alunoIdsValidos = alunoIds.filter(
        (id) => id && id.toString().trim() !== ""
      );

      // Primeiro, remover todos os alunos atuais da aula
      const { error: deleteError } = await supabase
        .from("aula_alunos")
        .delete()
        .eq("aula_id", aulaId);

      if (deleteError) {
        throw deleteError;
      }

      // Se no temos novos alunos para adicionar, retorna
      if (alunoIdsValidos.length === 0) {
        return true;
      }

      // Adicionar os novos alunos
      const novasRelacoes = alunoIdsValidos.map((alunoId) => ({
        aula_id: aulaId,
        aluno_id: alunoId,
      }));

      const { data, error: insertError } = await supabase
        .from("aula_alunos")
        .insert(novasRelacoes)
        .select();

      if (insertError) {
        throw insertError;
      }

      return true;
    } catch (err) {
      throw err;
    }
  },

  /**
   * Atualiza o nmero total de alunos em uma aula
   * @param {string} aulaId - ID da aula
   * @returns {Promise<number>} - Nmero total de alunos atualizado
   */
  async atualizarTotalAlunos(aulaId) {
    // Obter contagem de alunos
    const { count, error: countError } = await supabase
      .from("aula_alunos")
      .select("*", { count: "exact", head: true })
      .eq("aula_id", aulaId);

    if (countError) throw countError;

    // Atualizar o campo total_alunos na aula
    const { data, error: updateError } = await supabase
      .from("aulas")
      .update({
        total_alunos: count,
        totalAlunos: count, // Atualizar ambos os campos para garantir compatibilidade
      })
      .eq("id", aulaId)
      .select()
      .single();

    if (updateError) throw updateError;

    return data.total_alunos;
  },

  /**
   * Atualiza as observaes de um aluno em uma aula especfica
   * @param {string} aulaId - ID da aula
   * @param {string} alunoId - ID do aluno
   * @param {string} observacoes - Texto das observaes
   * @returns {Promise<Object>} - Os dados atualizados
   */
  async atualizarObservacoes(aulaId, alunoId, observacoes) {
    try {
      // Verificar se os IDs so vlidos
      if (!aulaId || !alunoId) {
        return {
          error: true,
          message: "IDs de aula ou aluno invlidos",
        };
      }

      // Atualizar as observaes na tabela de relacionamento
      const { data, error } = await supabase
        .from("aula_alunos")
        .update({ observacoes: observacoes })
        .eq("aula_id", aulaId)
        .eq("aluno_id", alunoId)
        .select()
        .single();

      if (error) {
        return {
          error: true,
          message: error.message || "Erro ao atualizar observaes",
        };
      }

      return data;
    } catch (err) {
      return {
        error: true,
        message: err.message || "Erro inesperado ao atualizar observaes",
      };
    }
  },
};

export default aulaAlunosService;
