import aulasService from "./aulas.service";

/**
 * Scheduler para finalizao automtica de aulas
 */
class AulaScheduler {
  constructor() {
    this.timer = null;
    this.isRunning = false;
  }

  /**
   * Inicia o agendamento para finalizar aulas em andamento
   */
  iniciarAgendamento() {
    if (this.isRunning) {
      return;
    }

    this.isRunning = true;

    // Configurar a verificao para rodar a cada minuto
    this.timer = setInterval(() => {
      this.verificarHorarioFinalizacao();
    }, 60000); // 60000ms = 1 minuto

    // Executar uma primeira verificao imediatamente
    this.verificarHorarioFinalizacao();
  }

  /**
   * Verifica se  horrio de finalizar aulas (23:59h) e executa a finalizao
   */
  verificarHorarioFinalizacao() {
    const agora = new Date();
    const horas = agora.getHours();
    const minutos = agora.getMinutes();

    // Se for 23:59h, finaliza todas as aulas em andamento
    if (horas === 23 && minutos === 59) {
      this.finalizarAulasEmAndamento();
    }
  }

  /**
   * Finaliza todas as aulas em andamento
   */
  async finalizarAulasEmAndamento() {
    try {
      const resultado = await aulasService.finalizarAulasEmAndamento();
      if (resultado.success) {
      } else {
        console.error("Erro na finalizao automtica:", resultado.error);
      }
    } catch (error) {
      console.error("Erro ao executar finalizao automtica:", error);
    }
  }

  /**
   * Para o agendamento
   */
  pararAgendamento() {
    if (this.timer) {
      clearInterval(this.timer);
      this.timer = null;
      this.isRunning = false;
    }
  }
}

// Criar instncia nica (mantida internamente)
const aulaScheduler = new AulaScheduler();

/**
 * Funo para iniciar o scheduler e retornar a funo de parada.
 * Garante que o scheduler seja iniciado apenas uma vez.
 */
const startAutoEndScheduler = () => {
  if (!aulaScheduler.isRunning) {
    aulaScheduler.iniciarAgendamento();
  }
  // Retorna a funo de parada vinculada  instncia correta
  return aulaScheduler.pararAgendamento.bind(aulaScheduler);
};

// Exportar a funo de inicializao como padro
export default startAutoEndScheduler;
