import React, { useEffect } from "react";
import "../styles/Sidebar.css";
import { useLocation } from "react-router-dom";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faUsers,
  faUser,
  faChevronLeft,
  faChevronRight,
  faSignOutAlt,
  faChalkboard, // cone para a Sala
  faFileAlt, // cone para Relatrios
} from "@fortawesome/free-solid-svg-icons";

const FORM_STORAGE_KEY = "cadastro_aluno_form_data";
const FORM_STATE_KEY = "cadastro_aluno_form_state";

function Sidebar({ navigate, collapsed, toggleSidebar, userRole, onLogout }) {
  const location = useLocation();
  const currentPath = location.pathname;

  // Funo para determinar a classe da seo ativa
  const getClassName = (path) => {
    return `sidebar-item ${currentPath === path ? "active" : ""}`;
  };

  // Controle do overlay e scroll do body em mobile
  useEffect(() => {
    const handleOverlay = () => {
      if (window.innerWidth <= 768 && !collapsed) {
        document.body.style.overflow = "hidden";
      } else {
        document.body.style.overflow = "";
      }
    };

    handleOverlay();
    window.addEventListener("resize", handleOverlay);

    return () => {
      window.removeEventListener("resize", handleOverlay);
      document.body.style.overflow = "";
    };
  }, [collapsed]);

  // Verificar se h formulrios em andamento no localStorage ao carregar o componente
  useEffect(() => {
    // Verificar se h formulrios salvos e restaurar a navegao
    try {
      const savedAlunoCadastro = localStorage.getItem(FORM_STORAGE_KEY);

      // Se existir um formulrio salvo e o usurio no est na pgina de cadastro
      if (savedAlunoCadastro && !currentPath.includes("/cadastros")) {
        // Perguntar ao usurio se deseja continuar o cadastro - usando window.confirm para evitar ESLint error
        if (
          window.confirm(
            "Voc tem um cadastro de aluno em andamento. Deseja continuar?"
          )
        ) {
          // Redirecionar para a seo de cadastros
          navigate("/cadastros");
        }
      }
    } catch (error) {
      console.warn("Erro ao verificar formulrios salvos:", error);
    }

    // Adicionar listener para eventos de visibilidade para manter seo atual
    const handleVisibilityChange = () => {
      if (document.visibilityState === "visible") {
        const lastPath = localStorage.getItem("lastActivePath");
        const savedForm = localStorage.getItem(FORM_STORAGE_KEY);

        // Se temos um formulrio salvo e a ltima seo era cadastros
        if (
          savedForm &&
          lastPath === "/cadastros" &&
          currentPath !== "/cadastros"
        ) {
          if (
            window.confirm(
              "Voc estava cadastrando um aluno. Deseja continuar?"
            )
          ) {
            navigate("/cadastros");
          }
        }
      }
    };

    document.addEventListener("visibilitychange", handleVisibilityChange);

    return () => {
      document.removeEventListener("visibilitychange", handleVisibilityChange);
    };
  }, [navigate, currentPath]);

  // Funo para fechar o sidebar e mudar de seo
  const handleNavigation = (path) => {
    // Se estamos na seo "cadastros" e queremos mudar para outra
    if (currentPath === "/cadastros" && path !== "/cadastros") {
      try {
        const savedForm = localStorage.getItem(FORM_STORAGE_KEY);
        const formState = localStorage.getItem(FORM_STATE_KEY);

        // Se existe um formulrio salvo e estamos em cadastros
        if (savedForm && formState === "open") {
          // Perguntar se realmente quer sair
          if (
            !window.confirm(
              "Voc est no meio de um cadastro. Se sair agora, o formulrio ser salvo para continuar depois. Deseja sair?"
            )
          ) {
            return; // No muda de seo
          }
          // Se confirmou que quer sair, atualiza o estado para "saved" em vez de "open"
          localStorage.setItem(FORM_STATE_KEY, "saved");
        }
      } catch (error) {
        console.warn("Erro ao verificar formulrio ao mudar de seo:", error);
      }
    }

    // Salvar a seo atual no localStorage
    try {
      localStorage.setItem("lastActivePath", path);
    } catch (error) {
      console.warn("Erro ao salvar caminho atual:", error);
    }

    // Fechar o sidebar antes de navegar (em qualquer tamanho de tela)
    if (!collapsed) {
      toggleSidebar();
    }

    // Depois de esconder o sidebar, navegue para a rota
    setTimeout(() => {
      navigate(path);
    }, 50); // Pequeno timeout para garantir que a UI reaja corretamente
  };

  // Funo para lidar com o clique no boto de logout
  const handleLogout = (e) => {
    e.preventDefault();

    // Fechar o sidebar no mobile antes de fazer logout
    if (window.innerWidth <= 768 && !collapsed) {
      toggleSidebar();
    }

    // Chamar a funo de logout
    if (onLogout) {
      onLogout();
    }
  };

  return (
    <>
      {/* Overlay para dispositivos mveis */}
      {!collapsed && (
        <div
          className={`sidebar-overlay ${!collapsed ? "active" : ""}`}
          onClick={toggleSidebar}
        />
      )}

      <div className={`sidebar ${collapsed ? "collapsed" : ""}`}>
        <div className="sidebar-header">
          {/* Removendo o logo do sidebar pois j existe no header */}
          <button className="collapse-button" onClick={toggleSidebar}>
            <FontAwesomeIcon
              icon={collapsed ? faChevronRight : faChevronLeft}
              className="collapse-icon"
            />
          </button>
        </div>

        <div className="sidebar-menu">
          {/* Sala como primeira opo no menu para todos os usurios */}
          <div
            className={getClassName("/sala")}
            onClick={() => handleNavigation("/sala")}
          >
            <FontAwesomeIcon icon={faChalkboard} className="sidebar-icon" />
            {!collapsed && <span>Sala</span>}
          </div>

          {/* Alunos */}
          <div
            className={getClassName("/alunos")}
            onClick={() => handleNavigation("/alunos")}
          >
            <FontAwesomeIcon icon={faUsers} className="sidebar-icon" />
            {!collapsed && <span>Alunos</span>}
          </div>

          {/* Mdulo de Professores */}
          <div
            className={getClassName("/professores")}
            onClick={() => handleNavigation("/professores")}
          >
            <FontAwesomeIcon icon={faUser} className="sidebar-icon" />
            {!collapsed && <span>Professores</span>}
          </div>

          {/* Relatrios - Apenas para Admin */}
          {userRole === "admin" && (
            <div
              className={getClassName("/relatorios")}
              onClick={() => handleNavigation("/relatorios")}
            >
              <FontAwesomeIcon icon={faFileAlt} className="sidebar-icon" />
              {!collapsed && <span>Relatrios</span>}
            </div>
          )}
        </div>

        {/* Boto de logout */}
        <div className="sidebar-footer">
          <div className="sidebar-item logout" onClick={handleLogout}>
            <FontAwesomeIcon icon={faSignOutAlt} className="sidebar-icon" />
            {!collapsed && <span>Sair</span>}
          </div>
        </div>
      </div>
    </>
  );
}

export default Sidebar;
