import React, { useState, useEffect, useCallback } from "react";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import {
  useNavigate,
  Routes,
  Route,
  Navigate,
  useParams,
} from "react-router-dom";
import supabase from "./config/supabaseConfig";
import Login from "./components/Login";
import Header from "./components/Header";
import Sidebar from "./components/Sidebar";
// Componentes
import Sala from "./components/Sala";
import Cadastros from "./components/Cadastros";
import Configuracoes from "./components/Configuracoes";
import HistoricoAlunos from "./components/HistoricoAlunos";
import GerenciamentoAlunos from "./components/GerenciamentoAlunos";
import GerenciamentoProfessores from "./components/GerenciamentoProfessores";
import DetalheCadastroAluno from "./components/DetalheCadastroAluno";
import EditarAluno from "./components/EditarAluno";
import Relatorios from "./components/Relatorios";
import { SalaProvider } from "./contexts/SalaContext";
import { CadastroAlunoProvider } from "./contexts/CadastroAlunoContext";
import startAutoEndScheduler from "./services/scheduler";
import "./App.css";

// Chaves do localStorage
const CADASTRO_FORM_STORAGE_KEY = "ironhouse_cadastroAlunoForm";
const AULA_ATUAL_STORAGE_KEY = "ironhouse_aulaAtual";

// Componente wrapper para DetalheCadastroAluno
const DetalheAlunoWrapper = ({ onNavigateBack }) => {
  const { id } = useParams();
  return <DetalheCadastroAluno alunoId={id} onNavigateBack={onNavigateBack} />;
};

// Componente wrapper para EditarAluno
const EditarAlunoWrapper = ({ navigate }) => {
  const { id } = useParams();
  return <EditarAluno alunoId={id} navigate={navigate} />;
};

const App = () => {
  const [user, setUser] = useState(null);
  const [userRole, setUserRole] = useState(null);
  const [loading, setLoading] = useState(true);
  const [sidebarCollapsed, setSidebarCollapsed] = useState(true); // Sempre inicia como true (colapsado/escondido)
  const [alunosEmAula, setAlunosEmAula] = useState([]);
  const navigate = useNavigate();

  // Efeito principal para autenticao e verificao de sesso
  useEffect(() => {
    let isMounted = true;

    // Funo para inicializar a aplicao (simplificada)
    const initializeApp = async () => {
      try {
        // 1. Obter sesso atual
        const { data: sessionData, error: sessionError } =
          await supabase.auth.getSession();

        if (sessionError) {
          console.error("[App.jsx] Erro ao obter sesso:", sessionError);
          if (isMounted) {
            setUser(null);
            setUserRole(null);
            navigate("/login");
            setLoading(false);
          }
          return;
        }

        const currentUser = sessionData?.session?.user;

        // 2. Se no tem usurio logado, mostrar login
        if (!currentUser) {
          if (isMounted) {
            setUser(null);
            setUserRole(null);
            navigate("/login");
            setLoading(false);
          }
          return;
        }

        // 3. Usurio logado, definir usurio e buscar role
        if (isMounted) {
          setUser(currentUser);
        }

        // 4. Buscar role do usurio
        let userRoleValue = null;
        try {
          const { data: profile } = await supabase
            .from("profiles")
            .select("role")
            .eq("id", currentUser.id)
            .single();

          if (profile) {
            userRoleValue = profile.role;
          }
        } catch (error) {
          console.error("[App.jsx] Erro ao buscar role:", error);
        }

        // 5. IMPORTANTE: Atualizar estados MESMO QUE HAJA ERROS nas etapas anteriores
        if (isMounted) {
          setUserRole(userRoleValue);
          setLoading(false);
        }
      } catch (error) {
        console.error("[App.jsx] Erro GERAL em initializeApp:", error);
        // Mesmo com erro, definir tela de login e sair do loading
        if (isMounted) {
          setUser(null);
          setUserRole(null);
          navigate("/login");
          setLoading(false);
        }
      }
    };

    // Chamar inicializao
    initializeApp();

    // Configurar listener de autenticao simplificado
    const { data: authListener } = supabase.auth.onAuthStateChange(
      (_event, session) => {
        if (!isMounted) return;

        const currentUser = session?.user;
        setUser(currentUser);

        if (!currentUser) {
          // Se no tem usurio (logout), ir para login
          setUserRole(null);
          navigate("/login");
          setLoading(false);
        } else if (_event === "SIGNED_IN") {
          // Se acabou de logar, buscar role
          supabase
            .from("profiles")
            .select("role")
            .eq("id", currentUser.id)
            .single()
            .then(({ data }) => {
              if (isMounted) {
                const role = data?.role || null;
                setUserRole(role);
                setLoading(false);
              }
            })
            .catch((error) => {
              console.error("[App.jsx] Erro ao buscar role aps login:", error);
              if (isMounted) {
                setUserRole(null);
                setLoading(false);
              }
            });
        }
      }
    );

    // Definir um fallback para garantir que loading sempre termine
    const timeoutId = setTimeout(() => {
      if (isMounted && loading) {
        setLoading(false);
      }
    }, 5000); // 5 segundos

    // Cleanup
    return () => {
      isMounted = false;
      clearTimeout(timeoutId);
      if (authListener && authListener.subscription) {
        authListener.subscription.unsubscribe();
      }
    };
  }, []); // Sem dependncias para evitar re-execues

  // Efeito para o Scheduler (separado)
  useEffect(() => {
    let isMounted = true;
    let stopScheduler = null;

    // Iniciar apenas se no estivermos no estado de loading inicial
    // e se o componente ainda estiver montado
    if (!loading && isMounted) {
      stopScheduler = startAutoEndScheduler();
    }

    return () => {
      isMounted = false;
      if (stopScheduler) {
        stopScheduler();
      }
    };
  }, [loading]); // Depende apenas do estado de loading

  // Adicionar um efeito para lidar com eventos de navegao customizada
  useEffect(() => {
    const handleNavigation = (event) => {
      const { secao } = event.detail;

      if (secao.startsWith("detalhe-aluno/")) {
        const id = secao.split("/")[1];
        navigate(`/detalhe-aluno/${id}`);
      } else if (secao.startsWith("editar-aluno/")) {
        const id = secao.split("/")[1];
        navigate(`/editar-aluno/${id}`);
      } else {
        navigate(`/${secao}`);
      }
    };

    window.addEventListener("navegarPara", handleNavigation);

    return () => {
      window.removeEventListener("navegarPara", handleNavigation);
    };
  }, [navigate]);

  // Funo para verificar se o usurio pode acessar uma seo
  const canUserAccessSection = useCallback(
    (section) => {
      // Seo sala  acessvel por padro para usurios logados
      if (section === "sala" || section === "cadastros") return true;

      // Verificaes adicionais baseadas em role
      if (!userRole) return false; // Para outras sees, precisa de role
      if (userRole === "admin") return true; // Admin acessa tudo
      if (userRole === "professor") {
        // Professor no acessa configuracoes nem relatrios
        return section !== "configuracoes" && section !== "relatorios";
      }
      return false; // Por padro, nega acesso
    },
    [userRole]
  );

  // Funo para lidar com o login
  const handleLogin = async (email, password) => {
    try {
      const { error } = await supabase.auth.signInWithPassword({
        email,
        password,
      });
      if (error) throw error;
      toast.success("Login realizado com sucesso!");
      navigate("/sala"); // Ir para sala aps login bem-sucedido
    } catch (error) {
      console.error("Erro no login:", error);
      toast.error(`Falha no login: ${error.message}`);
    }
  };

  // Funo para lidar com o logout
  const handleLogout = async () => {
    try {
      const { error } = await supabase.auth.signOut();
      if (error) throw error;
      // Limpeza de estados gerenciada pelo onAuthStateChange/useEffect
      toast.info("Voc foi desconectado.");
    } catch (error) {
      console.error("Erro no logout:", error);
      toast.error(`Falha ao sair: ${error.message}`);
    }
  };

  // Funo para atualizar os alunos em aula (passada para Sala)
  const atualizarAlunosEmAula = useCallback((alunos) => {
    setAlunosEmAula(alunos);
  }, []);

  // Funo para controlar o estado do sidebar
  const toggleSidebar = useCallback(() => {
    setSidebarCollapsed((prev) => {
      const newState = !prev;
      // Controlar o scroll do body quando o sidebar est aberto em mobile
      if (window.innerWidth <= 768) {
        if (newState) {
          // Fechando o sidebar
          document.body.style.overflow = "";
        } else {
          // Abrindo o sidebar
          document.body.style.overflow = "hidden";
        }
      }
      return newState;
    });
  }, []);

  // Funo para mudar a rota com verificaes
  const handleNavigate = useCallback(
    (path) => {
      // Extrair a seo base da rota
      const section = path.split("/")[1] || path;

      // 1. Verificar se est tentando sair de 'cadastros' com dados pendentes
      if (
        window.location.pathname.includes("/cadastros") &&
        !path.includes("/cadastros")
      ) {
        try {
          const savedForm = localStorage.getItem(CADASTRO_FORM_STORAGE_KEY);
          if (savedForm && savedForm !== "{}") {
            if (
              !window.confirm(
                "Voc tem dados no salvos no cadastro de aluno. Se sair agora, poder continuar depois. Deseja realmente sair?"
              )
            ) {
              return; // No muda de seo se o usurio cancelar
            }
          }
        } catch (error) {
          console.warn(
            "Erro ao verificar formulrio salvo ao sair da seo:",
            error
          );
        }
      }

      // 2. Verificar permisso de acesso
      if (!canUserAccessSection(section)) {
        console.warn(
          `Usurio ${userRole} no tem permisso para acessar ${section}.`
        );
        toast.warn("Voc no tem permisso para acessar esta rea.");
        return; // Impede a navegao
      }

      // 3. Navegar para a rota
      navigate(path);

      // 4. Fechar sidebar em mobile
      if (window.innerWidth <= 768) {
        setSidebarCollapsed(true);
        document.body.style.overflow = "";
      }
    },
    [navigate, userRole, canUserAccessSection]
  );

  // Componente para verificar autenticao e redirecionar, se necessrio
  const ProtectedRoute = ({ children, requiredSection = null }) => {
    if (loading) {
      return <div className="loading-indicator">Carregando...</div>;
    }

    if (!user) {
      return <Navigate to="/login" replace />;
    }

    if (requiredSection && !canUserAccessSection(requiredSection)) {
      toast.warn("Voc no tem permisso para acessar esta rea.");
      return <Navigate to="/sala" replace />;
    }

    return children;
  };

  return (
    <CadastroAlunoProvider>
      <div className="app-container">
        {user && !loading && (
          <>
            <Header
              user={user}
              onLogout={handleLogout}
              toggleSidebar={toggleSidebar}
              sidebarCollapsed={sidebarCollapsed}
            />
            <Sidebar
              userRole={userRole}
              navigate={handleNavigate}
              collapsed={sidebarCollapsed}
              toggleSidebar={toggleSidebar}
            />
          </>
        )}
        <main
          className={`main-content ${
            user && !loading && !sidebarCollapsed ? "" : "expanded"
          }`}
        >
          <div className="content-wrapper">
            <Routes>
              <Route
                path="/login"
                element={
                  loading ? (
                    <div className="loading-indicator">Carregando...</div>
                  ) : user ? (
                    <Navigate to="/sala" replace />
                  ) : (
                    <Login onLogin={handleLogin} />
                  )
                }
              />

              <Route
                path="/sala"
                element={
                  <ProtectedRoute>
                    <SalaProvider>
                      <Sala
                        userRole={userRole}
                        alunosEmAula={alunosEmAula}
                        atualizarAlunosEmAula={atualizarAlunosEmAula}
                      />
                    </SalaProvider>
                  </ProtectedRoute>
                }
              />

              <Route
                path="/alunos"
                element={
                  <ProtectedRoute requiredSection="alunos">
                    <GerenciamentoAlunos navigate={handleNavigate} />
                  </ProtectedRoute>
                }
              />

              <Route
                path="/cadastros"
                element={
                  <ProtectedRoute>
                    <Cadastros userRole={userRole} />
                  </ProtectedRoute>
                }
              />

              <Route
                path="/configuracoes"
                element={
                  <ProtectedRoute requiredSection="configuracoes">
                    <Configuracoes />
                  </ProtectedRoute>
                }
              />

              <Route
                path="/relatorios"
                element={
                  <ProtectedRoute requiredSection="relatorios">
                    <Relatorios />
                  </ProtectedRoute>
                }
              />

              <Route
                path="/historico"
                element={
                  <ProtectedRoute requiredSection="historico">
                    <HistoricoAlunos />
                  </ProtectedRoute>
                }
              />

              <Route
                path="/detalhe-aluno/:id"
                element={
                  <ProtectedRoute>
                    <DetalheAlunoWrapper onNavigateBack={handleNavigate} />
                  </ProtectedRoute>
                }
              />

              <Route
                path="/editar-aluno/:id"
                element={
                  <ProtectedRoute requiredSection="alunos">
                    <EditarAlunoWrapper navigate={handleNavigate} />
                  </ProtectedRoute>
                }
              />

              <Route
                path="/professores"
                element={
                  <ProtectedRoute>
                    <GerenciamentoProfessores navigate={handleNavigate} />
                  </ProtectedRoute>
                }
              />

              <Route path="/" element={<Navigate to="/sala" replace />} />

              <Route path="*" element={<Navigate to="/sala" replace />} />
            </Routes>
          </div>
        </main>
        <ToastContainer position="bottom-right" autoClose={3000} />
      </div>
    </CadastroAlunoProvider>
  );
};

export default App;
